<template>
  <el-card class="box-card">
    <el-button type="primary" @click="$router.push('/admin/main/delivery/add-drone')">Add drone</el-button>
    <el-button type="primary" @click="$router.push('/admin/main/delivery/refuel-drone')">Refuel drone</el-button>
    <el-button type="primary" @click="$router.push('/admin/main/delivery/join-swarm')">Join swarm</el-button>
    <el-button type="primary" @click="$router.push('/admin/main/delivery/leave-swarm')">Leave swarm</el-button>
    <el-button type="primary" @click="$router.push('/admin/main/delivery/fly-drone')">Fly drone</el-button>
    <el-button type="primary" @click="$router.push('/admin/main/delivery/takeover-drone')">Takeover drone</el-button>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="id">
        <el-input placeholder="id" v-model="param.id"></el-input>
      </el-form-item>
      <el-form-item label="tag">
        <el-input placeholder="tag" v-model="param.tag"></el-input>
      </el-form-item>
      <el-form-item label="fuel">
        <el-input placeholder="fuel" v-model="param.fuel"></el-input>
      </el-form-item>
      <el-form-item label="capacity">
        <el-input placeholder="capacity" v-model="param.capacity"></el-input>
      </el-form-item>
      <el-form-item label="sales">
        <el-input placeholder="sales" v-model="param.sales"></el-input>
      </el-form-item>
      <el-form-item label="flownBy">
        <el-input placeholder="flownBy" v-model="param.flownBy"></el-input>
      </el-form-item>
      <el-form-item label="swarmId">
        <el-input placeholder="swarmId" v-model="param.swarmId"></el-input>
      </el-form-item>
      <el-form-item label="swarmTag">
        <el-input placeholder="swarmTag" v-model="param.swarmTag"></el-input>
      </el-form-item>
      <el-form-item label="hover">
        <el-input placeholder="hover" v-model="param.hover"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchForm">Query</el-button>
      </el-form-item>
    </el-form>
    <el-table
        border
        ref="multipleTable"
        :data="tableList"
        tooltip-effect="dark"
        style="width: 100%"
    >
      <el-table-column
          align="center"
          label="id"
          prop="id"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="tag"
          prop="tag"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="fuel"
          prop="fuel"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="capacity"
          prop="capacity"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="sales"
          prop="sales"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="flownBy"
          prop="flownBy"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="swarmId"
          prop="swarmId"
          sortable
      >
        <template v-slot="scope">
          {{scope.row.swarmId ? scope.row.swarmId : 'n/a'}}
        </template>
      </el-table-column>
      <el-table-column
          align="center"
          label="swarmTag"
          prop="swarmTag"
          sortable
      >
        <template v-slot="scope">
          {{scope.row.swarmTag ? scope.row.swarmTag : 'n/a'}}
        </template>
      </el-table-column>
      <el-table-column
          align="center"
          label="hover"
          prop="hover"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="operation"
      >
        <template v-slot="scope">
          <el-button type="danger" size="small" @click="handleDelete(scope.row.id, scope.row.tag)">Delete</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="changePage"
    >
    </el-pagination>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        id: '',
        tag: '',
        fuel: '',
        capacity: '',
        sales: '',
        flownBy: '',
        swarmId: '',
        swarmTag: '',
        hover: '',
      },
      tableList: [],
      pageInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0
    }
  },
  mounted() {
    this.getTableList(this.pageInfo)
  },
  methods: {
    getTableList() {
      this.$axios.post('/drones/findAll', Object.assign(this.param, this.pageInfo)).then(res => {
        this.tableList = res.data.rows;
        this.total = res.data.count;
      })
    },
    searchForm() {
      this.pageInfo.pageNum = 1;
      this.getTableList();
    },
    changePage(e) {
      this.pageInfo.pageNum = e
      this.getTableList()
    },
    handleDelete(id, tag){
      this.$confirm('Are you sure to delete this?', 'Warning', {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning'
      }).then(() => {
        this.$axios.get('/drones/remove', {
          params: {
            id, tag
          }
        }).then(() => {
          this.$message({
            type: 'success',
            message: 'Delete successfully!'
          });
          this.getTableList();
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: 'Delete canceled'
        });
      });
    }
  },
}
</script>

<style scoped lang="scss">
.demo-form-inline, .el-pagination {
  margin-top: 10px;
}
</style>
